.App {
  margin-left: auto;
  margin-right: auto;
}

.Navbartop nav {
  /* padding: 0; */
}

.Navbartop img {
  height: 100px;
}

.LoginForm {
  width: 450px;
  margin-left: auto;
  margin-right: auto;
  padding: 60px;
}

#carouselID {
  position: relative;
  height: 30em;
}

.chapter #carouselDiv {
  position: absolute;
  top: 0px;
  width: 100%;

}
.chapter .carouselImg {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
}
.chapter #carouselDiv img {
  height: 30em;
  width: 30em;
}

.chapter #backgroundID {
  width: 100%;
  height: 100%;
  z-index: -1;  
}

.homeTable {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  text-align: right;
}

.homeTable td {
  padding: 10px;
  font-size: 1.5em;
}

#sponsorBtn {
  font-size: 1em;
  padding: 17px;
  padding-left: 37px;
  padding-right: 37px;
}

.purpleCard {
  --light: #c334eb;  
  --dark: #761e8f;
  --other: #FEFE33;
  --otherFore: #F7F7D4;
  border: solid 1px var(--dark);
}
.greenCard {
  --light: #2da873;
  --dark: #144e35;
  --other: #448D76;
  --otherFore: #F7DDD4;
  border: solid 1px var(--dark);
}
.orangeCard {
  --light: #ff5e00;
  --dark: #973801;
  --other: #B85B14;
  --otherFore: #D4E4F7;
  border: solid 1px var(--light);
}
.yellowCard {
  --light: #ded414;
  --dark: #b1a90e;
  --other: #868618;
  --otherFore: #E7F7D4;
  border: solid 1px var(--dark);
}
.redCard {
  --light: #db130f;
  --dark: #800a08;
  --other: #EA202C;
  --otherFore: #E7F7D4;
  border: solid 1px var(--dark);
}
.blueCard {
  --light: #1448d9;
  --dark: #1d6381;
  --other: #183BF0;
  --otherFore: #F7EBD4;
  border: solid 1px var(--dark);
}
.HomeContent .card, .HomeContent .card img, .DonateFood .card, .DonateFood .card img, .Sponsor .card  {
  border-radius: 1em;
}
.HomeContent .card-body, .DonateFood .card-body, .Sponsor .card-body button {
  color: var(--otherFore);
  background: -webkit-linear-gradient(var(--light), var(--dark));
  background: -moz-linear-gradient(var(--light), var(--dark));
  background: -o-linear-gradient(var(--light), var(--dark));
  background: linear-gradient(var(--light), var(--dark));
  border-radius: 1em;
}
.HomeContent .card-body, .DonateFood .card-body {
  padding-top: 40px;
  margin-top: -25px;  
}
.Sponsor .card {
  border: none;
}
.Sponsor .card-body button {
  padding: 20px;
  font-weight: bold;
  font-size: 1.3em;
  margin: 10px;
}
.HomeContent .card img, .DonateFood .card img {
  z-index: 5;
}
.HomeContent .card-body:hover, .DonateFood .card-body:hover, .Sponsor .card-body button:hover {
  background: -webkit-linear-gradient(var(--light), var(--light));
  background: -moz-linear-gradient(var(--light), var(--light));
  background: -o-linear-gradient(var(--light), var(--light));
  background: linear-gradient(var(--light), var(--light));
}
.HomeContent .card-body button, .DonateFood .card-body button {
  border: solid 1px var(--light);
  color: var(--light);
  background-color: var(--otherFore);
  font-weight: bold;
  border-radius: 1em;
  margin-left: auto;
  margin-right: auto;
}
.HomeContent .card-body button:hover, .DonateFood .card-body button:hover {
  background-color: var(--other);
  color: var(--dark);
}
.HomeContent .card-img-top:hover, .DonateFood .card-img-top:hover {
  opacity: 0.5;
}

#donation {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.donationTable , .foodPackTable{
  margin-left: auto;
  margin-right: auto;
}

.donationTable td, .foodPackTable td {
  padding: 5px;
}

.donationTable .card,  .foodPackTable .card {
  width: 18rem;
}

.donationTable img, .foodPackTable img {
  /* width: 100%; */
  height: 15em;
}

.DonateMoney .content, .DonateFood .content, .Confirmation .content, .Delivery .content, .Payment .content, .Custom .content, .Sponsor .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto; 
  min-height: 50em;
}

.boxDiv {
  border: solid 1px rgb(85, 85, 85);
  padding: 40px;
  border-radius: 25px;
}

