.paymentSection table {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}
.paymentSection #paymentTable {
  height: 100%;
}
.paymentSection #paymentProcess table {
  margin-left: auto;
  margin-right: auto;
}
.paymentSection #paymentProcess i {
  font-size: 40px;
  padding-right: 10px;
}
#PaymentSection img {
  height: 60px;
  margin-right: 10px;
  border: 1px solid black;
  border-radius: 10px;
  padding: 5px;
}

.productList {
  width: 100%;
  margin-left: auto;
  margin-right: auto; 
}
.productList .card {
  width: 15em;
  margin: 20px;
  padding: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.productList .card p, .productList .card img {
  width: 100%;
}
.productList .card img {
  height: 10em;
}
.productList .card button {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1em;
}

.btnClass {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
}
.btnClass button {
  width: 3em;
  font-weight: bold;
  display: inline-block;
}
.btnClass .qty {
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;

}

.MyCart .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 50em;
}

.MyCart li {
  max-width: 40em;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
}

#alertNotification {
  position: fixed;
  top: 60px;
  z-index: 10;
  min-width: 80%;
}

.Admin {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.addToOrder, .removeFromOrder {
  border: 1px solid grey; /* Green */
  padding: 0;
  padding: 2px 7px;
  margin-right: 10px;
  margin-left: 10px;
}

#myItems, #deliveryDetails {
  /* width: 50em; */
  margin-left: auto;
  margin-right: auto;
}

.Delivery form {
  width: 50%;
  width: 50em;
  margin-left: auto;
  margin-right: auto;
}
#deliveryDetails td {
  padding: 10px;
}
.orderFilter {
  display: inline-block;
}
.orderFilter #formInput {
  width: 10em;
}

#filteredOrder, .pendingOrders .order, .completedOrders .order {
  border: 1px solid grey;
  width: 50%;
  min-width: 50em;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
}

.buttonSpace {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.buttonSpace .left {
  float: left;
}

.buttonSpace .right {
  float: right;
}

.buttonSpace button {
  padding: 15px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 1.2em;
  font-weight: bold;

}

.btn {
  border-radius: 2em;
}

.form-control {
  border-radius: 2em;
}

.Sponsor .sponsorDiv {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

/* Mobile css */
.Mobile .carouselSlides {
  width: 100%;
}
.Mobile #carouselID {
  position: relative;
  height: 20em;
}
.Mobile .chapter #carouselDiv img {
  height: 20em;
  width: 20em;
}
.Mobile h3 {
  font-size: 5vw;
}
.Mobile .carouselSlides h3 {
  font-size: 4vw;
}
.Mobile .carouselSlides p {
  font-size: 3vw;
}
.Mobile .homeTable {
  width: 100%;
}
.Mobile .homeTable td {
  width: 50%;
}
.Mobile .homeTable p {
  font-size: 4vw;  
}
.Mobile .homeTable img {
  width: 100%;
}
.Mobile .donationTable td, .Mobile .productList td {
  float: left;  
}
.Mobile .donationTable .card, .Mobile .productList .card {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.Mobile .donationTable .card img, .Mobile .productList .card img {
  width: 100%;
}
.Mobile .paymentSection {
  width: 95%;
}
.Mobile .MyCart .content, .Mobile .Confirmation .content, .Mobile .Delivery .content, .Mobile .Payment .content {
  width: 95%;
}
.Mobile .MyCart .buttonSpace {
  width: 95%;
}
.Mobile .MyCart .buttonSpace button {
  padding: 10px;
  font-size: 1em;
}
.Mobile .Delivery form {
  width: 95%;
}
.Mobile #myItems {
  width: 100%;
  padding: 0;
}
.Mobile .recipient {
  width: 100%;
}
.Mobile .paymentSection {
  width: 100%;
  padding: 0;
}
.Mobile .paymentSection .paymentTable {
  width: 100%;
  float: left;  
}
.Mobile .paymentSection #paymentProcess {
  width: 100%;
  padding: 20px;
}
.Mobile .paymentSection #paymentProcess td {
  width: 100%;

}
.Mobile .paymentSection #myItems {
  width: 100%;
  top: 45em;
  left: 0;
}
.Mobile .paymentSection #myItems .boxDiv {
  padding: 10px;
  width: 100%;
} 
.Mobile .paymentSection #deliveryDetails {
  width: 100%;
  top: 75em;
  left: 0;
} 
.Mobile .paymentSection #deliveryDetails .boxDiv {
  padding: 10px;
}  

